.App {
    .sidebar {
        width: 16%;
        height: 100vh;
        background-color: #191D2B;
        position: fixed;
        // Border accent on the right side of the sidebar
        border-right: 1px solid #2e344e;
    }

    .main-content {
        width: 84%;
        margin-left: 16%;
        background-color: #10121B;
        min-height: 100vh;
        background-image: url(../img/dots.svg);
        display: grid;
        position: relative;
    }
}